import { FileInfo } from './types';

export interface FileHandlerConfig {
  type: 'samba' | 'dummy' | 'sftp' | 'sftp-reconnect' | string;
  remoteDirectory?: string;
  processedAction: 'move' | 'delete';
  processedDirectory?: string;
  processedPrefix?: string;
}

export interface ActiveFileHandlerInterface {
  get(): Buffer;
  close(): boolean;
}

export interface FileHandlerInterface {
  init(): Promise<void>;
  list(dir: string): Promise<Array<FileInfo>>;
  readFile(filepath: string): Promise<ActiveFileHandlerInterface>;
  writeFile(
    data: ActiveFileHandlerInterface,
    remotePath: string,
    filename: string,
  ): Promise<boolean>;
  deleteFile(filepath: string): Promise<boolean>;
  fileExists(location: string): Promise<boolean>;
  pathAsDsn(filepath: string): string;
}
